@import url(https://fonts.googleapis.com/css2?family=Reggae+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Reggae+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Reggae+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Reggae+One&display=swap);
* {
    margin: 0;
    padding: 0;
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
.loginContainer {
    width: 100vw;
    flex-direction: column;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.loginContainer img {
    width: 300px;
    height: 300px;
    margin-top: -200px;
    margin-bottom: -50px;
}

.login {
    display: flex;
    align-items: center;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 10px;
    justify-content: center;
    margin-top: 30px;
    border-radius: 8px;
    box-shadow: 0px 0px 5px black;
    cursor: pointer;
    font-weight: 500;
    font-family: 'Reggae One', cursive;
    transition: all .3s ease-in-out;
}

.login:hover {
    background-color: orange;
    color: white;
}

.login img {
    width: 20px;
    height: 20px;
    object-fit: cover;
    border-radius: 50%;
    margin: 5px;
}
.splashContainer {
    width: 100vw;
    height: 100vh;
    display: grid;
    place-items: center;
}

.splashContainer img {
    width: 300px;
    height: 300px;
    margin-bottom: -200px;
}

.splashContainer h2 {
    font-family: 'Reggae One', cursive;
    font-size: 2rem;
}

.splashContainer p {
    margin-top: -280px;
    margin-bottom: 20px;
    font-weight: 400;
    font-size: 1.3rem;
}
.header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    background-color: rgb(161, 253, 158);
    box-shadow: 0px 0px 10px darkgray;
}

.left,
.right {
    display: flex;
    align-items: center;
    margin: 20px;
}

.right img {
    margin-right: 20px;
}

.right p {
    font-size: 1.3rem;
    font-weight: 500;
}

.header img {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    object-fit: cover;
}

#profilePhoto {
    width: 50px;
    height: 50px;
    padding: 5px;
    background-color: lightgray;
    border-radius: 50%;
    object-fit: cover;
}
.sidebar {
    position: fixed;
    width: -100vw;
    overflow: hidden;
    z-index: 20;
    height: 100vh;
    background-color: rgba(0, 0, 255, .8);
    display: none;
    flex-direction: column;
    align-items: center;
    top: 0;
    left: 0;
    transition: all 1s ease;
}

.showSidebar {
    display: flex;
    z-index: 20;
    width: 300px;
}

.link {
    font-size: 1.5rem;
    display: block;
    width: 100%;
    border: none;
    border-bottom: 1px solid whitesmoke;
    text-align: center;
    font-family: 'Reggae One', cursive;
    margin-bottom: 20px;
    padding: 10px;
    cursor: pointer;
    text-decoration: none;
    color: white;
    background-color: transparent;
    transition: all .3s ease-in;
}

.activeLink {
    background-color: blue;
}

.link:hover {
    background-color: blue;
}

.close {
    width: 100%;
    text-align: right;
    margin-bottom: 20px;
    padding-top: 20px;
    padding-right: 20px;
}

.closeIcon {
    font-size: 1.5rem;
    color: white;
    padding: 2px;
    border-radius: 5px;
    background-color: red;
    opacity: .8;
    transition: all .3s ease-in-out;
    cursor: pointer;
}

.closeIcon:hover {
    opacity: 1;
}

.username {
    width: 100%;
    padding-left: 10px;
    color: white;
    margin-bottom: 40px;
    text-align: left;
    font-weight: 200;
}

.username h2:nth-of-type(2) {
    font-family: 'Reggae One', cursive;
    font-style: italic;
    margin-left: 40px;
}

.sidebarLogo {
    width: 70px;
    z-index: 10;
    background-color: white;
    height: 70px;
    border-radius: 50%;
    object-fit: cover;
}
.gridContainer {
    width: 70%;
    margin: 5px auto;
    display: grid;
    grid-gap: 10px;
    grid-template-rows: repeat(2, 1fr);
    grid-template-columns: repeat(2, 1fr);
}

.homeContainer {
    display: grid;
    width: 100%;
    place-items: center;
    padding-top: 80px;
}

.gridBox {
    display: flex;
    background-color: lightgreen;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    box-shadow: 0px 0px 5px black;
    min-height: 200px;
    font-family: 'Reggae One', cursive;
}

.gridBox p,
.gridBox strong {
    font-size: 1.1rem;
}

.gridBox p {
    font-weight: 500;
}

.gridBox img {
    width: 100%;
    height: 220px;
    object-fit: fill;
}

.salesContainer {
    display: flex;
    align-items: center;
    padding: 10px;
    margin-top: 10px;
    box-shadow: 0px 0px 5px black;
    font-family: 'Reggae One', cursive;
}

.addToProductbtn {
    padding: 8px 10px;
    background-color: blue;
    color: white;
    font-size: 1.2rem;
    transition: all .3s ease;
    border: 1px solid lightgray;
    outline: none;
    cursor: pointer;
    margin-top: 20px;
    border-radius: 6px;
}

.addToProductbtn:hover {
    box-shadow: 0px 0px 5px goldenrod;
    background-color: darkblue;
}

@media(max-width:600px) {
    .gridContainer {
        grid-template-columns: 1fr;
        grid-template-rows: repeat(4, 1fr);
    }
}
.addContainer {
    padding-top: 100px;
    width: 100%;
    display: grid;
    place-items: center;
}

.formContainer {
    margin-top: 20px;
    width: 300px;
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.formContainer input,
.formContainer button,
.formContainer textarea {
    display: block;
    width: 250px;
    cursor: pointer;
    border: 1px solid darkgray;
    border-radius: 5px;
    padding: 8px;
    outline: none;
    margin-bottom: 14px;
    box-shadow: 0px 0px 5px lightgray;
}

.formContainer input:focus,
.formContainer textarea:focus {
    border: 2px solid goldenrod;
    box-shadow: 0px 0px 5px gold;
}

.formContainer label {
    display: block;
    width: 250px;
}

.formContainer select,
.formContainer option {
    display: block;
    cursor: pointer;
    width: 250px;
    border: 1px solid darkgray;
    border-radius: 5px;
    padding: 8px;
    margin-bottom: 14px;
}

.formContainer button {
    background-color: rgba(0, 0, 255, .7);
    box-shadow: 0px 0px 4px purple;
    color: white;
    padding: 10px;
    font-size: 1.3rem;
    border-radius: 5px;
    transition: all .3s ease-in;
}

.formContainer button:hover {
    background-color: rgba(0, 0, 255, 1);
}

.formContainer button:disabled {
    cursor: not-allowed;
    background-color: gray;
}
